:root{--primaryColor: #E03E0E;--primaryColor2: #FA8717;--primaryColor3: #FEE6CF;--bannerColor: #EA5023;--fontFamily: 'Nunito', sans-serif;--templateColor: #E03E0E;--templateColor2: #FF8A73;--whiteColor: #ffffff;--lightColor: #F8F8FC;--darkColor: #7C7C80;--blackColor: #000000;--paragraphColor: #818992;--transition: .5s;--darkColor1: #474747;--blue: #4A50FF;--blueActive: #7D81F4;--pink: #D74A96;--pinkHover: #F076B8;--lightBlue: #6195FD;--lightBlueHover: #1763FB;--mint: #24B6B1;--mintHover: #2C9491;--green: #47821C;--greenHover: #5BB01C;--purpule: #7B70FF;--purpleHover: #5145E2;--yellow: #FFC736;--yellowHover: #FBB911;--breadcrumbtextcolor: #D1C4E9;--breadcrumbtextcoloractive: #fff}.navbar .nav__select{margin-bottom:0}.navbar .nav__select .form-select{background:#EFF0F7;border:none;border-radius:10px;-webkit-appearance:none;-moz-appearance:none;background-image:url("@src/assets/images/arrow-select.svg");background-repeat:no-repeat;background-position-x:100%;background-position-y:bottom}.navbar .nav__select .form-select #lang option[id="vi"]{background-image:url("@src/assets/images/vietnam-flag.svg");background-repeat:no-repeat;background-position-x:100%;background-position-y:bottom;-webkit-appearance:none;-moz-appearance:none}.navbar .nav__select .form-select #lang option[id="en"]{background-image:url("@src/assets/images/us-flag.svg");background-repeat:no-repeat;background-position-x:100%;background-position-y:bottom;-webkit-appearance:none;-moz-appearance:none}.navbar .nav__select .form-select:hover,.navbar .nav__select .form-select:focus,.navbar .nav__select .form-select:active{border:1px solid var(--primaryColor)}.navbar .nav__select .select__language{width:80px}
